<template>
	<div id="goods-post">
		<public-header title="商品编辑"></public-header>
		<div class="content">
			<div class="padding-10-tb padding-15-lr font-14 c-gray">基本信息</div>
			<van-cell-group>
				<van-cell title="商品图片" class="no-fix flex ">
					<div slot="right-icon" class="flex goods-thumb-wrap">
						<img class="goods-thumb " :src="thumb.url" alt="" v-if="thumb.url">
						<img class="goods-thumb " src="static/img/add_pic.png" alt="" v-else>
						<input class="weui-uploader__input" type="file" multiple="multiple" accept="image/*" @change="onUploadImage($event)">
					</div>
				</van-cell>
				<van-field
					v-model="goodsTemp.title"
					label="商品名称"
					placeholder="请输入商品名称"
				/>
				<van-field
					v-model="goodsTemp.price"
					label="外卖价格"
					placeholder="给商品定个好价格"
				/>
				<van-field v-if="util.checkplugin('svip')"
					v-model="goodsTemp.svip_price"
					label="超级会员价格"
					placeholder="超级会员价格小于外卖现价才能生效"
				/>
				<van-field
					v-model="goodsTemp.ts_price"
					label="店内价格"
					placeholder="给商品定个好价格"
				/>
				<van-field
					v-model="goodsTemp.box_price"
					label="餐盒费"
					placeholder="请输入餐盒费"
				/>
				<van-field
					v-model="goodsTemp.unitnum"
					label="起购数量"
					placeholder="商品最小购买份数"
				/>
				<van-field
					v-model="goodsTemp.unitname"
					label="单位"
					placeholder="份/盘/斤"
				/>
				<van-field
					v-model="goodsTemp.label"
					label="标签"
					placeholder="热卖,新品,爆款等,不超过两个字"
				/>
				<!--<van-field-->
					<!--v-model="goodsTemp.comment_good"-->
					<!--label="好评数"-->
					<!--placeholder="请输入商品好评数量"-->
				    <!--type="number"-->
				<!--/>-->
			</van-cell-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">商品设置</div>
			<van-cell-group>
				<van-cell title="商品分类">
					<div slot="right-icon" class="flex" @click="onTogglePopup('category')">
						<span>{{goodsTemp.category_title ? goodsTemp.category_title : '请选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="商品类型">
					<div slot="right-icon" class="flex" @click="onTogglePopup('type')">
						<span>{{goodsTemp.type_title ? goodsTemp.type_title : '请选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="换购设置" v-if="goodsTemp.huangou_status == '1'">
					<div slot="right-icon" class="flex" @click="onTogglePopup('huangou')">
						<span>{{goodsTemp.huangou_title ? goodsTemp.huangou_title : '请选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="上架">
					<iswitch :value="goodsTemp.status" condition-open="1" condition-close="0" slot="right-icon" text-open="上架" text-close="下架"  @change="jsToggleSwitch" keys="goodsTemp.status"></iswitch>
				</van-cell>
				<van-cell title="热销">
					<iswitch :value="goodsTemp.is_hot" condition-open="1" condition-close="0" slot="right-icon" text-open="热销" text-close="关闭" @change="jsToggleSwitch" keys="goodsTemp.is_hot"></iswitch>
				</van-cell>
			</van-cell-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">商品描述</div>
			<van-cell-group>
				<van-field
					type="textarea"
					v-model="goodsTemp.description"
					placeholder="请输入商品描述"
					rows="3"
				/>
			</van-cell-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">其他信息</div>
			<van-cell-group>
				<van-field
					v-model="goodsTemp.total"
					label="总库存"
					placeholder="请输入总库存"
				/>
				<van-field
					v-model="goodsTemp.displayorder"
					label="排序"
					placeholder="数字越大越靠前"
				/>
			</van-cell-group>
			<template v-for="(item, index) in goodsTemp.options">
				<van-cell-group class="margin-10-t">
					<van-field
						v-model="goodsTemp.options[index].name"
						label="规格"
						placeholder="输入规格名称, 例如大杯"
					/>
					<van-field
						v-model="goodsTemp.options[index].price"
						label="价格"
						:placeholder="'输入商品价格(' + Lang.dollarSignCn + ')'"
					/>
					<van-field v-if="util.checkplugin('svip')"
						v-model="goodsTemp.options[index].svip_price"
						label="会员价"
						:placeholder="'输入商品会员价格(' + Lang.dollarSignCn + ')'"
					/>
					<van-field
						v-model="goodsTemp.options[index].total"
						label="库存"
						placeholder="请输入库存, -1表示不限制"
					/>
					<van-field
						v-model="goodsTemp.options[index].total_warning"
						label="库存预警"
						placeholder="请输入库存"
					/>
					<van-field
						v-model="goodsTemp.options[index].displayorder"
						label="排序"
						placeholder="请输入排序"
					/>
				</van-cell-group>
				<div class="delete-option" @click="onDelOptions(index)">删除商品规格</div>
			</template>
			<div class="add-option" @click="onAddOptions">
				<span class="flex">
					<i class="icon icon-add font-18"></i>
					<span>添加商品规格</span>
				</span>
			</div>
			<template v-for="(item, index) in goodsTemp.attrs">
				<van-cell-group class="margin-10-t">
					<van-field
						v-model="goodsTemp.attrs[index].name"
						label="属性名称"
						placeholder="例如: 辣度"
						icon="delete"
						@click-icon="onDelAttr(index)"
					/>
					<van-field
						v-model="goodsTemp.attrs[index].goodsLabel"
						label="属性标签"
						placeholder="请填写属性标签，如：微辣"
						icon="add"
						@click-icon="onAddAttr(index)"
					/>
					<div class="attrs-list" v-if="item.label && item.label.length > 0">
						<div class="attrs-item" v-for="(label, labIndex) in item.label">
							{{label}}
							<i class="icon icon-close c-gray" @click="onDeleteAtt(index, labIndex)"></i>
						</div>
					</div>
				</van-cell-group>
			</template>
			<div class="add-attrs" @click="onAddAttrs">
				<span class="flex">
					<i class="icon icon-add font-18"></i>
					<span>添加商品属性</span>
				</span>
			</div>
			<div class="padding-15">
				<van-button size="normal" block class="bg-info font-16" @click="onSubmit">保存</van-button>
				<van-button size="normal" block class="bg-danger font-16 margin-15-t" @click="util.jspost({url: 'manage/goods/index/del', data: {id: id}, confirm: '确定删除此商品吗', successUrl: util.getUrl({path: 'pages/goods/index', query: {cid: goodsTemp.cid}})})" v-if="id > 0">删除</van-button>
			</div>
		</div>
		<van-popup v-model="popupStatus.category" position="bottom">
			<van-picker
				show-toolbar
				title="所属分类"
				:columns="categorys"
				value-key="title"
				@cancel="onTogglePopup('category')"
				@confirm="onConfirmCategory"
			/>
		</van-popup>
		<van-popup v-model="popupStatus.type" position="bottom">
			<van-picker
				show-toolbar
				title="商品类型"
				:columns="types"
				value-key="title"
				@cancel="onTogglePopup('type')"
				@confirm="onConfirmType"
			/>
		</van-popup>
		<van-popup v-model="popupStatus.huangou" position="bottom">
			<van-picker
				show-toolbar
				title="换购设置"
				:columns="huangou_types"
				value-key="title"
				@cancel="onTogglePopup('huangou')"
				@confirm="onConfirmHuangou"
			/>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
			iswitch
		},
		data() {
			return {
				showPreLoading: true,
				id: 0,
				popupStatus: {
					category: false,
					type: false,
					huangou: false
				},
				thumb: {},
				goodsTemp: {
					title: '',
					price: 0,
					ts_price: 0,
					box_price: 0,
					svip_price: 0,
					unitname: '',
					unitnum: 1,
					label: '',
					total: -1,
					status: 1,
					is_hot: 0,
					type: 3,
					description: '',
					category_title: '',
					type_title: '',
					options: [],
					attrs: [],
					huangou_status: 0,
					huangou_type: 1,
					huangou_title: '支持换购和购买'
				},
				categorys: {},
				types: {},
				huangou_types: {}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/goods/index/post',
					data: {
						id: that.id,
						_account_perm: 1
					},
					success: (res) => {
						that.categorys = res.categorys;
						that.goodsTemp = Object.assign(that.goodsTemp, res.goods);
						that.types = res.type;
						that.huangou_types = res.huangou_types;
						if(that.id > 0) {
							that.thumb.url = that.goodsTemp.thumb_;
						}
					}
				});
				if(this.util.isCloud()) {
					this.util.requestPermission({
						perm: 'camera'
					});
					this.util.requestPermission({
						perm: 'photos'
					});
				}
			},
			onTogglePopup(key) {
				this.popupStatus[key] = !this.popupStatus[key];
			},
			onConfirmCategory(picker) {
				if(picker.parentid > 0) {
					this.goodsTemp.cid = picker.parentid;
					this.goodsTemp.child_id = picker.id;
				} else {
					this.goodsTemp.cid = picker.id;
					this.goodsTemp.child_id = 0;
				}
				this.goodsTemp.category_title = picker.title;
				this.onTogglePopup('category');
			},
			onConfirmType(picker) {
				this.goodsTemp.type = picker.id;
				this.goodsTemp.type_title = picker.title;
				this.onTogglePopup('type');
			},
			onConfirmHuangou(picker) {
				this.goodsTemp.huangou_type = picker.id;
				this.goodsTemp.huangou_title = picker.title;
				this.onTogglePopup('huangou');
			},
			onUploadImage(e) {
				var that = this;
				that.util.image({
					obj: e,
					success: function(obj, result) {
						if(result.url && result.filename) {
							that.thumb = {
								url: result.url,
								filename: result.filename,
							};
							that.thumb = Object.assign({}, that.thumb);
							that.goodsTemp.thumb = that.thumb.filename;
						}
					},
					options: {channel: 'h5'}
				});
			},
			jsToggleSwitch(data) {
				var that = this;
				that.util.jsToggleSwitch({
					vue: that,
					key: data.keys,
					value: data.value
				});
			},
			onAddOptions() {
				var	optionItem = {
						id: 0,
						name: '',
						price: 0,
						svip_price: 0,
						total: -1,
						total_warning: 0,
						displayorder: 0,
					};
				this.goodsTemp.options.push(optionItem);
			},
			onDelOptions(index) {
				this.goodsTemp.options.splice(index, 1);
				//this.goodsTemp = Object.assign({}, this.goodsTemp);
			},
			onAddAttrs() {
				var	attrsItem = {
					name: '',
					goodsLabel: '',
					label: []
				};
				this.goodsTemp.attrs.push(attrsItem);
			},
			onDelAttr(index) {
				var that = this;
				that.goodsTemp.attrs.splice(index, 1);
			},
			onAddAttr(index) {
				var that = this;
				if(!that.goodsTemp.attrs[index].goodsLabel) {
					that.util.$toast('请输入有效的属性标签', '', 1000);
					return false;
				}
				that.goodsTemp.attrs[index].label.push(that.goodsTemp.attrs[index].goodsLabel);
				that.goodsTemp.attrs[index].goodsLabel = '';
			},
			onDeleteAtt(index, labIndex) {
				var that = this;
				that.goodsTemp.attrs[index].label.splice(labIndex, 1);
			},
			onSubmit() {
				var that = this;
				if(!that.goodsTemp.title) {
					that.util.$toast('商品名称不能为空', '', 1000);
					return false;
				}
				if(!that.goodsTemp.price) {
					that.util.$toast('商品外卖价格不能为空', '', 1000);
					return false;
				}
				if(!that.goodsTemp.ts_price) {
					that.util.$toast('商品店内价格不能为空', '', 1000);
					return false;
				}
				if(!that.goodsTemp.cid) {
					that.util.$toast('请选择商品所属分类', '', 1000);
					return false;
				}
				if (!that.goodsTemp.type) {
					that.util.$toast('请选择商品所属类型', '', 1000);
					return false;
				}
				setData({
					vue: that,
					url: 'manage/goods/index/post',
					data: {
						id: that.id,
						params: JSON.stringify(that.goodsTemp)
					},
					redirect: that.util.getUrl({path: 'pages/goods/index'})
				});
			}
		},
		mounted(){
			this.onLoad();
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.id) {
				this.id = this.query.id;
			}
		}
	}
</script>

<style>
	#goods-post .goods-thumb{
		width: 60px;
		height: 60px;
		border-radius: 3px;
		margin-left: 30px;
	}
	#goods-post .goods-thumb-wrap{
		position: relative;
	}
	#goods-post .weui-uploader__input{
		display: block;
		width: 60px;
		height: 60px;
		opacity: 0;
		position: absolute;
		top: 0px;
		left: 30px;
	}
	#goods-post .iswitch{
		height: 24px;
	}
	#goods-post .add-option{
		width: 100%;
		height: 44px;
		padding: 0 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		margin-top: 10px;
		background-color: #fff;
		font-size: 14px;
		color: #33aafc;
	}
	#goods-post .add-attrs{
		width: 100%;
		height: 44px;
		padding: 0 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		margin-top: 10px;
		background-color: #fff;
		font-size: 14px;
		color: #33aafc;
	}
	#goods-post .delete-option{
		height: 44px;
		line-height: 44px;
		color: #FF2C4C;
		font-size: 14px;
		text-align: center;
		background-color: #fff;
	}
	#goods-post .attrs-list{
		display: flex;
		flex-flow: wrap;
		align-items: flex-start;
		justify-content: flex-start;
		padding: 10px 15px 5px 10px;
		font-size: 14px;
	}
	#goods-post .attrs-list .attrs-item{
		color: #33aafc;
		border: 1px solid #33aafc;
		padding: 2px 5px;
		margin: 0 5px 5px 0;
	}
	#goods-post .van-cell__value .van-icon-delete{
		color: #ff2d4b;
	}
	#goods-post .van-cell__value .van-icon-add{
		color: #33aafc;
	}
</style>


